.footer{
    position: relative;
    background: #333333;
    padding: 27px 0px;
}

.container{
	max-width: 1150px;
	margin: 0px auto;
}

.footer p{
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #6E6F6F;
    margin: 0;
    font-weight: 500;
}


@media screen and (max-width:767px){
    .footer {
        padding: 49px 0px;
    }
}