.thankyou{
    position: relative;
    padding: 50px 0px 90px;
    min-height: calc(100vh - 346px);
    display: flex;
    align-items: center;
}

.container{
    max-width: 936px;
    margin: 0px auto;
    padding: 0px 15px;
}

.thankyou img{
    max-width: 304px;
    margin: 0px auto;
    display: block;
}

.thankyou h2{
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.01em;
    color: #333333;
    margin: 0;
    padding-bottom: 20px;
    padding-top: 10px;
}

.thankyou p{
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    color: #333333;
    max-width: 636px;
    margin: 0px auto;
}


@media screen and (max-width:767px){
    .thankyou{
        padding: 35px 0px 70px;
        min-height: inherit;
    }
}