.topBanner{
    padding: 66px 0px 112px;
    position: relative;
}

.topBanner:before{
    content: "";
    background: #F5F8F8;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;
    clip-path: polygon(0 0, 100% 0%, 100% 75%, 0 100%);
}
.topBannerContainer{
    max-width: 966px;
    padding: 0px 15px;
    margin: 0px auto;
    position: relative;
}

.topBannerTitle h3{
    font-weight: 400;
    font-size: 40px;
    line-height: 104.5%;
    text-align: center;
    letter-spacing: 0.01em;
    color: #333333;
    margin: 0;
    padding-bottom: 20px;
}

.topBannerTitle h3 span{
    color: #F35C5C;
}

.topBannerTitle p {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.01em;
    margin: 0;
    padding-bottom: 35px;
    color: #333333;
}

.formGrid {
    background: #FFFFFF;
    border: 2px solid rgba(98, 67, 206, 0.18);
    box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
    border-radius: 30px;
    padding: 50px;
    max-width: 584px;
    margin: 0px auto;
}

.formGrid ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    gap: 36px;
    padding-bottom: 40px;
}

.formGrid ul li.active svg path{
    fill: #6243CE;
}
.formGrid h4 {
    font-weight: 400;
    text-align: center;
    letter-spacing: 0.01em;
    color: #333333;
    padding-bottom: 15px;
    font-size: 26px;
    line-height: 120%;
    margin: 0;
}

.formGrid h4 span {
    color: #6243CE;
}

.formGroup{
    margin-bottom: 20px;
}
.formGroup label {
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #333333;
    margin: 0;
    display: block;
    padding-bottom: 15px;
    text-align: left;
    font-family: 'DM Serif Display', serif;
}

.buttonGrid{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
.backBtn {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    border: none;
    letter-spacing: -0.01em;
    background: transparent;
    color: #CED0D0;
    padding: 12px 15px;
}

.submitBtn{
    background: #F35C5C;
    border-radius: 53px;
    max-width: 206px;
width: 100%;
    padding: 12px 25px;
    color: #fff;
    border: 1px solid #F35C5C;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #FFFFFF;
}

.formScroll{
    height: 175px;
    overflow-y: scroll;
    overflow-x: hidden;
    margin-bottom: 25px;
}


.formScroll::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

.formScroll::-webkit-scrollbar
{
	width: 0px;
	background-color: #F5F5F5;
}

.formScroll::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}









/************************* How It Works ***************************/

.howItWork{
    position: relative;
}

.howItWorkContainer{
    max-width: 1070px;
    padding: 0px 15px;
    margin: 0px auto;
}

.howItWork h3{
    font-weight: 400;
font-size: 33px;
line-height: 120%;
padding-bottom: 70px;
text-align: center;
letter-spacing: 0.01em;
margin: 0;
color: #333333;
}

.howItWorkGrid{
    display: grid;
    grid-template-columns: repeat(4,minmax(0,1fr));
    gap: 30px;
}

.howItWorkCard{
    background: #FFFFFF;
    box-shadow: 0px 2px 27px -1px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    padding: 40px 20px;
    text-align: center;
}

.howItWorkCard h4{
margin: 0;
padding: 20px 0px 10px;
font-weight: 400;
font-size: 20px;
line-height: 120%;
letter-spacing: 0.01em;
color: #333333;

}

.howItWorkCard p{
    font-weight: 500;
font-size: 16px;
line-height: 24px;
text-align: center;
letter-spacing: -0.01em;
margin: 0;
color: #333333;

}




/************************* Testimonials ***************************/

.testimonial{
    position: relative;
    padding-top: 100px;
}


.testimonialContainer{
    max-width: 1290px;
    margin: 0px auto;
}
.testimonial h3{
    font-weight: 400;
font-size: 33px;
line-height: 120%;
padding-bottom: 70px;
text-align: center;
letter-spacing: 0.01em;
margin: 0;
color: #333333;
}

.testimonialGrid{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    gap: 30px;
}

.testimonialCard{
    background: #FFF5F5;
border-radius: 11px;
padding: 30px 20px;
}


.testimonialCard p{
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    font-feature-settings: 'calt' off;
    color: #333333;
    opacity: 0.7;
    margin: 0;
    padding: 20px 0px 23px;
}

.testimonialCard h4{
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #333333;
    
    display: flex;
    align-items: center;

}

.testimonialCard h4 img{
    margin-right: 10px;
}

.rating{
    display: flex;
    gap: 6px;
}
/************************* Call To Action *************************/

.callToAction{
    position: relative;
    padding: 100px 0px;
}

.callToActionContainer{
    max-width: 1166px;
    margin: 0px auto;
}

.callToActionGrid{
    background: #F5F8F8;
    border-radius: 30px;
    padding: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
}

.callToActionContent h2{
    font-weight: 400;
    font-size: 33px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.01em;
    color: #333333;
    max-width: 340px;
    text-align: center;
}

.callToActionContent h2 span{ 
    color: #6243CE;
}

.callToActionContent a{
    background: #F35C5C;
box-shadow: 0px 4px 6px -1px rgba(0, 0, 0, 0.12), 0px 2px 4px -1px rgba(0, 0, 0, 0.07);
border-radius: 74px;
padding: 16px 32px;
max-width: 190px;
width: 100%;
margin: 0px auto;
font-weight: 700;
font-size: 16px;
line-height: 24px;
letter-spacing: -0.01em;
display: block;
text-align: center;
text-decoration: none;

color: #FFFFFF;
}